<!-- SystemSetting -->
<template>
    <div class="myPromise">
        <el-button @click="startPrimiseSuc" type="primary">开启一个Promise,2s后resolve</el-button>
        <el-button @click="startPrimiseFail" type="danger">开启一个Promise,1s后reject</el-button>

        <el-button @click="getRes1" type="success">捕获Promise的结果</el-button>
        <!-- <el-button @click="getRes2" type="success">捕获Promise2的结果</el-button> -->
    </div>
</template>


<script>
export default {
    name: 'MyPromise',
    data() {
        return {
            p:'',
            p1:'',
            p2:''
        }
    },
    methods: {
        startPrimiseSuc() {
            this.p = new Promise((resolve) => {
                setTimeout(() => {
                    resolve('成功1!')
                }, 2000)
            })
        },
        startPrimiseFail() {
            this.p = new Promise((resolve, reject) => {
                setTimeout(() => {
                    reject('失败2!')
                }, 1000)
            })
        },
        getRes1() {
            this.p
            .then(data => {
                this.$message.success('primise成功!', data)
            })
            .catch((data) => {
                this.$message.error('primise失败!', data)
            })
            console.log(this.p)
        },
        // getRes2() {
        //     this.p
        //     .then(data => {
        //         this.$message.success('primise成功!', data)
        //     })
        //     .catch((data) => {
        //         this.$message.error('primise失败!', data)
        //     })
        // }
    },
}
</script>


